.footer-wrapper {
  position: relative;
  bottom: 0;
  z-index: 999;
  width: 100%;
  max-width: 100vw;
  min-height: 60px;
  padding: 20px;
  .line-item {
    display: inline-block;
  }
  ul {
    list-style: none;
    padding: 0;
    margin: 0;
    position: relative;
    z-index: 999;
    li{
      display: inline-block;
    }
    .list-item {
      display: inline-block;
      padding: 0 10px;
      cursor: pointer;
      text-transform: none;
      line-height: 1.75;
    }
  }
  .footer-body {
    display: flex;
    .footer-right {
      width: 18%;
      min-width: 200px;
      text-align: left;
    }
    .footer-left {
      width: 82%;
      li{
        display: inline-block;
      }
    }
    @media (max-width: 1700px) {
      .footer-right {
        width: 23%;
      }
      .footer-left {
        width: 77%;
      }
    }
    @media (max-width: 1400px) {
      .footer-right {
        width: 28%;
      }
      .footer-left {
        width: 72%;
      }
    }
    @media (max-width: 1100px) {
      .footer-right {
        width: 33%;
      }
      .footer-left {
        width: 67%;
      }
    }
    @media (max-width: 900px) {
      .footer-right {
        width: 43%;
      }
      .footer-left {
        width: 57%;
      }
    }
    @media (max-width: 600px) {
      flex-direction: column;
      .footer-right,
      .footer-left {
        width: 100%;
        text-align: center;
      }
      .social-icons {
        margin-right: 10px;
      }
      ul {
        margin: 5px 0;
        .list-item {
          padding: 5px 0;
        }
      }
    }
  }
  .footer-head {
    .line-item {
      font-size: 20px;
    }
    @media (max-width: 600px) {
      ul{
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }
  }
  .MuiButtonBase-root {
    padding: 0;
    margin-bottom: 4px;
  }
  .footer-rights{
    display: flex;
    justify-content: center;
    span::before{
      content: '';
      width: 100%;
      height: 3px;
      background-image: radial-gradient(ellipse at center,rgba(255,255,255,.1) 0,transparent 100%);
      position: absolute;
      display: block;
      left: 0;
      top: -7px;
    }
  }
}
